import React, { PureComponent } from 'react'
import { connect } from "react-redux";
import { DragDropContext, Droppable } from "react-beautiful-dnd";
import { Link } from "react-router-dom";
import { sort, setActiveBoard } from "../action";


import TrelloList from "../components/TrelloList";
import TrelloCreate from "../components/TrelloCreate";


class BoardPage extends PureComponent {
    componentDidMount() {
      const { boardID } = this.props.match.params;
      this.props.dispatch(setActiveBoard(boardID));
    }
    onDragEnd = result => {
      const { destination, source, draggableId, type } = result;
  
      if (!destination) {
        return;
      }
  
      this.props.dispatch(
        sort(
          source.droppableId,
          destination.droppableId,
          source.index,
          destination.index,
          draggableId,
          type
        )
      );
    };
  
    render() {
      const { lists, cards, match, boards } = this.props;
      const { boardID } = match.params;
      const board = boards[boardID];
      const listOrder = board.lists;
  
      return (
        <DragDropContext onDragEnd={this.onDragEnd}>
            <div className="bloc-smile">
            <Link to="/"><a className="smile"></a></Link>
            </div>
            <h2 className='name-board'>{board.title}</h2>
          <Droppable droppableId="all-lists" direction="horizontal" type="list">
            {provided => (
              <div className='lists-container'
                {...provided.droppableProps}
                ref={provided.innerRef}
              >
                {listOrder.map((listID, index) => {
                  const list = lists[listID];
                  if (list) {
                    const listCards = list.cards.map(cardID => cards[cardID]);
                    return (
                      <TrelloList
                        listID={list.id}
                        key={list.id}
                        title={list.title}
                        cards={listCards}
                        index={index}
                      />
                    );
                  }
                })}
                {provided.placeholder}
                <TrelloCreate list />
            </div>
            )}
          </Droppable>
        </DragDropContext>
      );
    }
  }


const mapStateToProps = state => ({
    lists: state.lists,
    cards: state.cards,
    boards: state.boards
  });
  
  export default connect(mapStateToProps)(BoardPage);